.container .curso {
  width: 33rem;
  height: auto;
  border-radius: 0.8rem;
  box-shadow: 0 0.2rem 0.2rem rgba(0, 0, 0, 0.2);
  overflow: hidden;
  text-align: center;
  transition: all 0.25s;
  margin-top: 2rem;
  margin-bottom: 5rem; }

.container .curso:hover {
  transform: translateY(-15px);
  box-shadow: 0 1.2rem 1.6rem rgba(0, 0, 0, 0.2); }

.container .curso img {
  width: 33rem;
  height: 22rem; }

.container .curso h4 {
  font-weight: 600; }

.container .curso p {
  padding: 0 1rem;
  font-size: 16px;
  font-weight: 300; }

.header {
  background-color: blue;
  padding: 1rem 0 3rem 0; }

.contenido-header .enlaces {
  display: flex;
  gap: 3rem;
  margin-left: 0rem; }
  @media (min-width: 780px) {
    .contenido-header .enlaces {
      margin-left: 10rem;
      margin-right: 8rem; } }
  .contenido-header .enlaces a {
    height: 9rem;
    width: 9rem; }

.barra {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 1rem; }
  @media (min-width: 780px) {
    .barra {
      flex-direction: row;
      justify-content: space-around; } }

.navegacion {
  display: flex;
  flex-direction: column;
  margin-top: 5rem;
  gap: 3rem; }
  @media (min-width: 780px) {
    .navegacion {
      flex-direction: row;
      gap: 15rem;
      margin-top: 0;
      margin-right: 9rem;
      align-items: flex-end; } }
  .navegacion .efecto {
    font-family: "Audiowide", sans-serif;
    color: #FFFFFF;
    text-decoration: none;
    font-size: 2rem; }
    .navegacion .efecto::after {
      content: '';
      display: block;
      width: 0;
      height: 2px;
      background: #FFFFFF;
      transition: width .3s; }
    .navegacion .efecto:hover::after {
      width: 100%;
      transition: width .3s; }
    .navegacion .efecto:hover {
      color: #90FFDC; }
  .navegacion .basicas {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 3rem; }
    @media (min-width: 780px) {
      .navegacion .basicas {
        flex-direction: row;
        align-items: flex-start; } }
  .navegacion .sesion {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 3rem; }
    @media (min-width: 780px) {
      .navegacion .sesion {
        flex-direction: row;
        align-items: flex-start; } }

.footer {
