html {
  font-size: 62.5%;
  box-sizing: border-box;
}
*, *:before, *:after {
  box-sizing: inherit;
}
body {
  font-family: $fuente_principal;
  font-size: 1.6rem;
  line-height: 1.8;
  background-image: linear-gradient(to top, $grisClaro 0%, $blanco 100%);
}
p {
  color: $negro;
  font-size: 2rem;
}
.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}
.container{
  width: 100%;
  max-width: 120rem;
  height: 43rem;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin: auto;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3 {
  font-family: $fuente_principal;
  margin: $separacion / 2 0;
  font-weight: 300;
  text-align: center;
  
}

h1 {
  font-size: 3.8rem;
}
h2 {
  font-size: 3.4rem;
}
h3 {
  font-size: 3rem;
}
h4 {
  font-size: 2.6rem;
}